}
.dropdown-menu {
  background-color: black;
  color: white;
  border-radius: 10px;
  padding: 5px;
}
.btn2 {
  background-color: black;
  color: white;
  border-radius: 10px;
  margin: 5px;
}
input {
  color: black;
  border-radius: 10px;
  padding: 5px;
}
.center1 {
  text-align: center;
  margin: 0 0 0 0;
}
body {
  background-image: url("./images/workspace.jpg");
  text-align: center;
  background-size: cover;
  background-color: gray;
  margin: auto;
  color: black;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: black;
  margin-top: 60px;
}
.user {
  color: white;
  font-weight: bold;
  font-size: 20px;
  float: left;
  margin: 8px;
}
#myNavbar {
  width: 350 px;
  padding: 5px;
}
li {
  list-style: none;
}
.getout {
  opacity: .8;
  border-radius: 10px;
  margin: 10px 10px 10px 0;

  background-color: black;
  color: white;
  float: right;
}
.dropdown:hover .dropdown-menu {
  display: block;
}
.firstRow {
  height: 4%;
}
#app {
  margin: 0;
  padding: 0;
}



